@font-face {
  font-family: Soleil;
  src: url(/BryantPro/BryantPro-Bold.otf);
}

@font-face {
  font-family: Soleil;
  src: url(/Museo/MuseoSans.otf);
}
button{
  border: none;
  font-size: 75%;
  padding: 1vw 3vw;
  border-radius: 5rem;
  color:dodgerblue;
  background-color: rgb(240, 240, 240);
}
html{
  font-size: 16px;
}
body{
  text-align: center;
  font-family: 'Soleil';
  color: white;
  min-height: 100vh;
  background: linear-gradient(180deg, rgba(9,9,121,1) 0%,rgb(1, 0, 29) 100%);
background-repeat: no-repeat;
  font-weight:500;
  place-items: center;
  align-content: center;
}

.quiz{
  margin: 0vw 5vw;
}

.quiz-container{
  display: flex;
  flex-direction: column;
  align-items: center;
margin: 0;
padding: 0;
}

.quiz img{
  max-width: 8rem;
  padding-top: 12vw;
}

.input{
  margin: 10vw auto 2vw auto;
padding: 2vw 3vw;
border: none;
border-radius: 1.5rem;
font-size: 4vw;
 }

.comenzar{
  font-family: 'Soleil';
  margin: 3vw;
}

table{
  align-items: center;
  margin: 7.5vw;
  margin-bottom: 5vw;
  font-size: 4vw;
  padding: 2vw ;
 }
 table, th {
  color: white;
  border: .5vw solid white;
  padding: 4vw 7vw;
  border-collapse: collapse;
}

.quiz-container h1{
  margin: 5vw auto;
  font-size: 8vw;
  line-height: 9vw;
}

.quiz-container p{
  margin: 0 auto ;
  padding: 0;
  line-height:0vw;
  font-size: 4vw;
}

.preguntas-section{
  margin-top: 1rem;
}

.pregunta-texto{
  font-size: 1.5rem;
}

.opciones img{
  width: 15rem;
  height: 8rem;
  text-align: center;
  margin: .5rem;
}

.opciones{
  color: black;
  font-family: 'Soleil';
  width: 18rem;
  border-radius: 1rem;
  font-size: .7rem;
  margin: 2vw;
  padding: 3% 0em;
}

button:disabled{
  color: black;
  background-color: rgba(255, 255, 255, 0.5);
}

.respuestas{
line-height: 0;
margin: 0;
padding: 0;
}

.siguiente{
  font-family: 'Soleil';
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.score-section{
  margin-top: 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  place-items: center;
}

.score-section p{
  font-size: 5vw;
  margin: 2vw;

}

.nota{
  background-color: rgba(255, 255, 255, 0.75);
  color: black;
  max-width: 18rem;
  border-radius: 1rem;
}
.nota img{
  max-width: 13rem;
  margin: auto;  
  border-radius: .5rem;
}
.nota h1{
  font-size: 10vw;
  margin: 3vw;
  font-weight: bolder;
}
.nota p{
  font-size: 6vw;
 margin: 2vw 10vw 3vw 10vw;
}
.reiniciar{
  margin: 2em 0em;
}





@media only screen and (min-width:1000px) {
  
body{
  text-align: center;
  font-family: 'Soleil';
  color: white;
  min-height: 100vh;
  background: linear-gradient(180deg, rgba(9,9,121,1) 0%,rgb(1, 0, 29) 100%);
background-repeat: no-repeat;
  font-weight:500;
  place-items: center;
  align-content: center;
}
.quiz{
  margin: 0;
}

.quiz-container{
  display: flex;
  flex-direction: column;
  align-items: center;
margin: 0;
padding: 0;
}

.quiz img{
  max-width: 15vw;
  margin: 5vw auto 0vw auto;
  padding: 0;
}

.quiz-container h1{
  margin: 3vw auto;
  font-size: 3vw;
  line-height: 0vw;
}

.quiz-container p{
  margin: 0 auto ;
  padding: 0;
  line-height:0vw;
  font-size: 2vw;
}
.input{
  margin: 3vw auto 1.5vw auto;
padding: .75vw 3vw .75vw 1vw;
border: none;
border-radius: 1.5rem;
font-size: 1vw;
 }

.comenzar{
  background-color: rgba(255, 255, 255, 0.5);
  font-family: 'Soleil';
  font-size: 1vw;
  padding: 1vw 2vw;
  border-radius: 5rem;
  margin: 0vw;

}

table{
  align-items: center;
  margin: 3vw;
  margin-bottom: 5vw;
  font-size: 2vw;
 }
 table, th {
  color: white;
  border: 2px solid white;
  padding: 1vw 3vw;
  border-collapse: collapse;
}


button{
  background-color: rgba(255, 255, 255, 0.25);
  color: black;
}

button:hover{
  background-color: rgba(255, 255, 255, 0.75);
  color: dodgerblue;
}

.pregunta-texto{
  margin-top: .5em;
  font-size:4rem;
}


.opciones{
  display: inline;
  font-family: 'Soleil';
  width: 40rem;
  border: none;
  border-radius: .75rem;
  font-size: 1.5rem;
  margin:1em;
  padding: 3% 0;
}

.opciones:disabled
{background-color: rgba(255, 255, 255, 0.75);
  color: black;
}

button:disabled{
  background-color: rgba(255, 255, 255, 0.5);
  color: black;
}
.opciones img{
  width: 20em;
  height: 12em;
  text-align: center;
  margin: 1em;
}
.respuestas{
line-height: 0;
margin: 0;
padding: 0;
}

.siguiente{
  font-family: 'Soleil';
  margin: .5rem;
  border: none;
  font-size: 1.2vw;
  padding: 1vw 2vw;
  border-radius: 5rem;
}

.score-section{
  display: flex;
  justify-content: center;
  flex-direction: column;
  place-items: center;
  margin: 1vw;
}

.score-section p{
  margin: 1vw;
  font-size: 1.5vw;
}

.nota{
  text-align: center;  
  background-color: #ecececfb;
  color: black;
  border-radius: .75rem;
  max-width: 30vw;
  
}

.nota p{
  margin: .5rem;
}

.nota img{
  max-width: 20vw;
  margin: 0vw 2vw;
  padding: 0;
  
}

.nota h1{
  font-size: 3.5vw;
  margin: 1vw;
  font-weight: bolder;
}
.nota p{
  font-size: 1.8vw;
 margin: .75vw 2vw 1.25vw 2vw;
}

.reiniciar{
  font-family: 'Soleil';
  margin: 0;
  margin-top: 1.25em;
  border: none;
  font-size: 1.2vw;
  padding: 1vw 2vw;
  border-radius: 5rem;
}




}